.number {
  padding-left: 6px;
}

.number p {
  font-size: 12px;
  margin-bottom: 3px;
}

.reverse {
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

@media screen and (max-width: 650px) {
  .number p {
    font-size: 9px;
  }
}
